:root{
  --main-color:yellow;
}

.top_60{
  margin-top: 60px;
}
.top_30{
  margin-top: 30px;
}
.mb_30{
  margin-bottom: 30px !important;
}
.p_30{
  padding: 30px;
}
.pt_45{
  padding-top: 45px;
}
.pb_45{
  padding-bottom: 45px;
}
.p_50{
  padding: 50px !important;
}
@media (max-width:991px){
  .top_60{
  margin-top: 30px;
  }
}


.main-content{
  background-color: white;
  min-height: 100vh;
  border-radius: 6px;
  -moz-border-radius:6px;
  -webkit-border-radius:6px;
  margin-bottom: 30px;
  
}

.section_title{
  position: relative;
  display: inline-block;
  padding: 0 3px;
}
.section_title .section_title_text{
  font-size: 18px;
  font-weight: 600;
  position: relative;
  margin: 0;
}
.section_title span{
  position: absolute;
  height: 7px;
  width: 100%;
  bottom: 0;
  opacity: 0.4;
  left: 0;
  background-color: yellow;

}
.section{
  padding-left: 30px;
  padding-right: 30px;
}

.graybg{
 
  background-color: #fafafa;
 
  
}